* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*------------ HEADER ----------- */

.encabezado {
  background-color: black;
  color: white;
}

.encabezado div {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.encabezado h1 {
  margin-left: 2rem;
  padding-top: 1.5rem;
  padding-bottom: 0.8rem;
}

.encabezado h4 {
  padding-top: 2rem;
  padding-left: 50rem;
}

.img{
  padding-top: 1rem;
  padding-right: 2rem;
  padding-bottom: 1rem;
  width: 5rem;
}

/*------------ SECTION ----------- */

.seccion {
  padding-right: 3rem;
  padding-left: 3rem;
}

.hora {
  margin-top: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: flex-end;
}

.tabla {
  width: 50%;
  margin-top: 2.5rem;
  margin-bottom: 3.2rem;
  margin-left: 17rem;
  border: 2px solid red;
}

.tablehead {
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  display: flex;
  justify-content: space-around;
  border: 2px solid lightskyblue;
}

.tablebody {
  margin-top: 0.25rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  border: 2px solid green;
}


.fila1 {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  padding-left: 1rem;
  display: flex;
  justify-content: space-around;
}

.fila2 {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  padding-left: 1rem;
  display: flex;
  justify-content: space-around;
}

.fila3 {
  margin-top: 0.5rem;
  margin-bottom: 0.1rem;
  padding-left: 1rem;
  display: flex;
  justify-content: space-around;
}

.button {
  margin-right: 0.5rem;
  margin-bottom: 0.1rem;
  background-color: #3080E5;
  color: white;
  padding: 0.3rem 1rem;
  border-radius: 0.5rem;
}

.seccion p {
  margin: 1rem;
}

/*------------ FOOTER ----------- */

.pie {
  margin-top: 3rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  background-color: black;
  color: white;
}

.derechos {
  display: flex;
  justify-content: center;
}